:host{
  width: 100%;
  height: 100%;
}
.container{
  width: 90%;
  height: 90%;
  padding: 10px;
  margin: 0 auto;
  border-radius: 10px;
  box-shadow: 0 20px 40px rgba(7, 20, 35, 0.2);
  display: flex;
  flex-direction: column;
}


.options{
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  gap: 10px;
  padding: 0 10px;
}

.spacer{
  flex: 1;
}

select{
  padding: 5px;
  border: 1px solid #020929;
  border-radius: 3px;
}

.options label,
.options select{
  font-family: 'Poppins', sans-serif;
}
.input-wrapper{
  display: flex;
  align-items: center;
  gap: 10px;
}
input[type="color"]{
  // -webkit-appearance: none;
  // -moz-appearance: none;
  appearance: none;
  background-color: transparent;
  width: 40px;
  height: 28px;
  border: none;
  cursor: pointer;
}

input[type="color"]::-webkit-color-swatch {
  border-radius: 15px;
  box-shadow: 0 0 0 2px #ffffff, 0 0 0 3px #020929;
}

input[type="color"]::-moz-color-swatch {
  border-radius: 15px;
  box-shadow: 0 0 0 2px #ffffff, 0 0 0 3px #020929;
}

#text-input{
  width: 90%;
  max-width: 90%;
  max-height: 120px;
  margin:10px;
  border: 1px solid #dddddd;
  padding: 20px;
  // flex: 1;
  overflow-y: auto;
  white-space: pre-wrap; /* 줄 바꿈을 유지 */
  line-height: 1.5; /* 줄 높이 설정 */
  word-wrap: break-word;
  overflow-wrap: break-word;
  box-sizing: border-box;
}

.active{
  background-color: #e0e9ff;
}

.menu-container{
  display: flex;
  flex-wrap: wrap;
  gap: 5px;
  overflow: hidden;
  background-color: #faf9fd;
  // padding: 5px;
}

::ng-deep .custom-menu{
  background-color: #faf9fd !important;
  overflow: hidden;
}

